* {
  box-sizing: border-box;
}

html, body {
  background-color: #5a5a5a;
  margin: 0;
  font-family: "Goudy Bookletter 1911", serif;
  font-style: normal;
}

h1.title {
  color: #292929;
  text-shadow: 5px 4px #fff;
  -webkit-text-stroke: .1px #000;
  text-align: center;
  font-size: 5rem;
  line-height: 3.3rem;
  text-decoration: none;
}

h1.title:hover {
  text-shadow: 5px 4px #494949;
  color: #fff;
}

a {
  text-decoration: none;
}

.main {
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
  display: flex;
}

.featured-story {
  text-align: center;
  background-color: #302e2e8c;
  border-radius: 10px;
  width: 100%;
  max-width: 1000px;
  margin-bottom: 20px;
  transition: transform .3s;
  box-shadow: 3px 3px 10px #fff3;
}

.featured-story img {
  border-radius: 10px 10px 0 0;
  max-width: 100%;
  height: auto;
}

.featured-story h2 {
  color: #fff;
  margin: 15px;
  font-size: 2.5rem;
}

.headline-container {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  display: flex;
}

.headline {
  color: #fff;
  text-align: center;
  background-color: #302e2e8c;
  border-radius: 10px;
  width: 280px;
  margin: 10px;
  padding: 10px;
  transition: transform .3s;
  box-shadow: 2px 2px 4px #ff00001a;
}

.headline:hover {
  transform: scale(1.1);
  box-shadow: 3px 3px 5px #fff;
}

.headline img {
  border-radius: 10px 10px 0 0;
  max-width: 100%;
  height: auto;
}

.headline h2 {
  margin-top: 10px;
  font-size: 20px;
}

.headline:active {
  color: red;
}

.featured-story:hover {
  transform: scale(1.05);
  box-shadow: 3px 3px 3px 5px #fff;
}

.header-container {
  justify-content: center;
  align-items: center;
  padding: 20px;
  display: flex;
}

#searchbar {
  border-radius: 10px;
  width: 30%;
  margin-left: 20px;
  padding: 10px;
  transition: width .3s ease-in-out, margin-left .3s ease-in-out;
  position: relative;
}

input[type="text"] {
  border: 1px solid #ccc;
  border-radius: 10px;
  width: 100%;
}

input[type="text"]:focus {
  outline: none;
  width: 150%;
  margin-left: -10%;
}

#suggestions {
  z-index: 1000;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 30%;
  max-height: 200px;
  padding: 0;
  list-style-type: none;
  display: none;
  position: absolute;
  top: 15%;
  overflow-y: auto;
}

#suggestions li {
  cursor: pointer;
  padding: 10px;
}

#suggestions li:hover {
  background-color: #f0f0f0;
}

.no-result {
  color: red;
  text-align: center;
  margin: 20px 0;
}

footer {
  justify-content: center;
  margin-top: 15px;
  display: flex;
}

footer a {
  color: #fff;
}

footer a:hover {
  color: red;
}

@media (width <= 768px) {
  h1.title {
    font-size: 3rem;
  }

  #searchbar {
    width: 90%;
    margin-top: 15px;
    margin-left: 0;
  }

  .header-container {
    flex-direction: column;
    align-items: center;
  }

  .headline-container {
    flex-direction: column;
  }

  .headline {
    width: 100%;
  }
}
